import axios from 'axios'
import { Message } from 'iview'
import router from '../router/router'
/**
 * GET请求数据
 * @param {string} url - 请求的接口路径
 * @param {object} objectParams - 请求的参数对象
 * @param {string} [customMsg] - 自定义提示消息
 * @returns {Promise}
 */
export function doGetGeneral (url, objectParams, customMsg = '') {
  return new Promise((resolve, reject) => {
    const instance = axios.create({
      baseURL: window.store.host,
      timeout: 5000000000000,
      headers: {
        'Authorization': 'bearer ' + window.localStorage['access_token'],
        'Content-Type': 'application/x-www-form-urlencoded'
      }
    })
    const options = {
      url: url,
      method: 'get',
      params: objectParams
    }
    instance(options).then(response => {  // axios返回来结果
      const res = response.data
      if (res.code === 401) { // 没有登录或者token失效
        Message.error(res.message, 3)
        router.replace({name: 'login'})
      } else if (res.code === 403) { // 没有权限
        Message.error(res.message, 3)
        router.replace({name: '403'})
      } else {
        resolve(res)    // axios返回来的结果有效可用，则resolve这个结果给Promise实例
      }
    }).catch(error => {
      if (customMsg) {
        Message.error(customMsg, 3)
      } else if (error.response.data.message) {
        Message.error(error.response.data.message, 3)
      } else {
        Message.error(error.toString(), 3)
      }
      reject(error)
    })
  })
}

/**
 * POST请求数据（对象参数方式）
 * @param {string} url - 请求的接口路径
 * @param {object} objectParams - POST的参数，该参数是一个object对象
 * @param {string} [customMsg] - 自定义提示消息
 * @returns {Promise}
 */
export function doPostWithObjectParams (url, objectParams, customMsg = '') {
  return new Promise((resolve, reject) => {
    const qs = require('qs')
    axios.post(url, qs.stringify(objectParams), {
      baseURL: window.store.host,
      timeout: 5000000000000,
      headers: {
        'Authorization': 'bearer ' + window.localStorage['access_token']
      }
    }).then(function (response) {
      const res = response.data
      if (res.code === 401) { // 没有登录或者token失效
        Message.error(res.message, 3)
        router.replace({name: 'login'})
      } else if (res.code === 403) { // 没有权限
        Message.error(res.message, 3)
        router.replace({name: '403'})
      } else {
        resolve(res)
      }
    }).catch(function (error) {
      if (customMsg) {
        Message.error(customMsg, 3)
      } else if (error.response.data.message) {
        Message.error(error.response.data.message, 3)
      } else {
        Message.error(error.toString(), 3)
      }
      reject(error)
    })
  })
}



